
body{
  background: url("../img/login_bg.jpg") center top fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 100%;
}

.master-lunch-text{

  font: 215px "Reklame Script Regular", "Open Sans", sans-serif;
  color: #fff;
  width: 100%;
  max-width: 900px;
  height: 100%;
  max-height: 80px;
  position: absolute;
  top: 34%;
  left: 50%;
  margin: -40px 0 0 -450px;

  &.error-title{
    font-size: 130px;
    text-align: center;
  }


  .apostrophe{
    font-family: "RoadRadio-Thin";
  }
}

.overlay-wall{
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
}

.ui.segment.login-form-segment{
  background: none;

  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -140px 0 0 -200px;
  padding: 0;
  box-shadow: none;
  border: none;

  input{
    border: 1px solid #fff;
    color: #fff;
    font: 18px "Open Sans", sans-serif;
    text-align: center;
    font-weight: 300;
    background: transparent;
    padding: 10px;
    margin-top: 30px;
    list-style: none;
    text-shadow: 0 1px 1px rgba(255,255,255,0.25);
    width: 100%;
    border-radius: 0;
  }

  .login-button{
    border: none;
    font: 23px "helveticaNeueCyr Medium", "Open Sans", sans-serif;
    color: #fff;
    margin-top: 35px;
    width: 100%;
    height: 60px;
    background: none;
    border-radius: 30px;
    box-shadow: 0 0 0 1px #b0afaf;
    -moz-box-shadow: 0 0 0 1px #b0afaf;
    -webkit-shadow: 0 0 0 1px #b0afaf;

    &:focus{
      color: #fff;
      background: none;
    }
  }
}